@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;


.gui{
    transform: perspective(500px) rotateX(5deg) rotateY(-5deg) scale(1.05);
    box-shadow: 10px 10px 10px #0f0f11;
}

.glow-button{
    box-shadow: 0 0px 15px #0095f2;
}

.glow-button:hover{
    box-shadow: 0 0px 20px #0095f2;
}

@media only screen and (min-width: 0){
    .gui{
        transform: scale(1);
    }
}

@media only screen and (min-width: 1024px){
    .gui{
        transform: perspective(500px) rotateX(5deg) rotateY(-5deg) scale(0.9);
    }
}


@media only screen and (min-width: 1280px){
    .gui{
        transform: perspective(500px) rotateX(5deg) rotateY(-5deg) scale(1.05);
    }
}